/* 0. Reset ================================= */
@import 'reset';


/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background: #fff;
	
}

p{
	color: #454343;
}



/* 2. Content ================================= */

.mobile-menu{
	opacity: 0.5;
	right: 30px;
	top: 30px;
	z-index: 15;
	position: absolute;
	&:hover{
		opacity: 1;
	}
	@media screen and (min-width: 768px){
		display: none;
	}

	img{
		width: 55px;
		
	}
}

.nav{

	a{
		text-decoration: none;
		color: #383737;
		font-family: avenir next;
		text-transform: uppercase;
		letter-spacing: 0.02em;
		transition: 1s ease;

		&:hover{
			color: #fff;
		}
	}

	//desktop nav
	@media screen and (min-width: 768px){
		display: flex;

		ul{
			background-color: #fbbe28;
			padding: 10px;
			position: absolute;
			z-index: 22;
			right: 0;
			transform: translateY(-20px);
			@media screen and (min-width: 1224px){
				padding: 12px;
				transform: translateY(-26px);
			}

			li{
				padding-right: 30px;
				display: inline-block;
				@media screen and (min-width: 1024px){
					padding-right: 50px;
				}
				@media screen and (min-width: 1224px){
					padding-right: 50px;
				}

				&:first-child{
					padding-left: 30px;
				}

				a{
					font-size: 0.8em;
					@media screen and (min-width: 1024px){
						font-size: 1em;
					}
					@media screen and (min-width: 1224px){
						font-size: 1.2em;
					}
				}
			}
		}

	}

	//mobile nav
	@media screen and (max-width: 767px){
		display: none;
		

		&.show{
			position: absolute;
			display: flex;
			width: 100%;
			height: 100vh;
			text-align: center;
			left: 0;
			top: 0;
			background-color: #fbbe28;
			z-index: 13;
			
			ul{
				position: absolute;
				top: 50%;
				left: 50%;
				margin: 0;
				transform: translate(-50%, -50%);
				z-index: 22;

				li{
					opacity: 0;
					padding-bottom: 90px;

					a{
						font-size: 2em;
					}
				}
				
			}

		}
	}
	
}


