.background1{
	position: relative;
	transition: .3s ease;

	h1{
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		color: #fff;
		font-size: 24px;
		text-align: center;
		font-family: avenir next;
		text-transform: uppercase;
		letter-spacing: 0.2em;

		&::after{
			content: "";
			background-color: #fbbe28;
			height: 8px;
			width: calc(100% - 0.2em);
			top: 52%;
			position: absolute;
			left: 50%;
			transform: translateX(-50%) translateX(-0.1em);
			@media screen and (max-width: 767px){
				margin-top: 20px;
			}
			@media screen and (min-width: 768px){
				margin-top: 20px;
			}
		}
	}

	&:hover{
		background-image: url(../img/smart.jpg);
		background-repeat: no-repeat;
		height: 100%;
		width: 29.1796875%;
		background-size: 100% auto;
		@media screen and (max-width: 767px){
			height: 33.33333vh;
			width: 100vw;
			background-position: top;
		}
	}
}

