.logo {
	position: fixed;
	@media screen and (max-width: 573px){
		transform: translateY(-46%);
	}
	@media screen and (min-width: 574px){
		transform: translateY(-20%);
	}
	position: absolute;
	left: 20px;
	width: 100%;
	z-index: 2;
	
	
	@media screen and (min-width: 768px){
		transform: translateY(-40px);
	}
	@media screen and (min-width: 1024px){
		transform: translateY(-53px);
	}
	@media screen and (min-width: 1224px){
		transform: translateY(-63px);
	}
	@media screen and (min-width: 1440px){
		transform: translateY(-70px);
	}

	img{
		@media screen and (max-width: 767px){
			width: 18%;
		}
		width: 10%;
		transition: .5s ease;
		
		z-index: 2;
		&:hover {
			transform: rotate(90deg);
			transform-origin: center;
		}
	}
}


.vertical-line{
	transition: 4s;
	position: relative;
	//hide dots 
	.dot1, .dot2, .dot3, .dot4{
		display: none;
	}

	&.show{
		transform: scale(1.1); 
		top: 100px;
		left: 13%;
		position: fixed;
		height: 240px;
		width: 2px;
		background-color:#fbbe28;
		@media screen and (min-width: 768px){
			top: 80px;
			left: 4.5%;
		}
		@media screen and (min-width: 1024px){
			top: 100px;
		}
		@media screen and (min-width: 1224px){
			top: 120px;
		}
		@media screen and (min-width: 1440px){
			top: 135px;
		}
		//show dots on logo click
		.dot1, .dot2, .dot3, .dot4{
			display: block;
		}
	}

	.dot1, .dot2, .dot3, .dot4{
		z-index: 2;
		border: 2px solid #fbbe28;
		background-color: #fff;
		width: 11px;
		height: 11px;
		border-radius: 90%;
		left: 11.8%;
		position: fixed;
		top: 100px;
		left: -5px;
	}
	
	
	.dot1{
		top: 60px;
		&::after{
			content: "why smart?";
			white-space: nowrap;
			left: 20px;
			position: absolute;
			font-family: avenir next;
			letter-spacing: 0.35em;
			font-weight: bold;
			font-size: 0.8em;
		}
	}
	
	.dot2{
		top: 120px;
	}
	
	.dot3{
		top: 180px;
	}
	
	.dot4{
		top: 240px;
	}
}



h2{
	white-space: nowrap;
	font-family: avenir;
	text-transform: uppercase;
	color: #fff;
	background-color: #2c2c2c;
	letter-spacing: 0.19em;
}

.middle-top{
	display: flex;
	justify-content: center;
	margin: 30% auto;
	position: relative;
	@media screen and (min-width: 1000px){
		margin: 20% auto;
	}

	.headline{
		text-align: center;
		position: absolute;
		@media screen and (min-width: 768px){
			position: absolute;
			right: 10.9375%;
		}

		h2{
			padding: 15px 22px;
			font-size: 0.8em;
			@media screen and (min-width: 768px){
				padding: 15px 38px;
			}
			@media screen and (min-width: 1000px){
				font-size: 0.9em;
			}
			@media screen and (min-width: 1150px){
				font-size: 1em;
			}
			@media screen and (min-width: 1281px){
				font-size: 1.1em;
			}
			
		}
	}

	.flexrow-items{
		flex-direction: column;
		width: 75.6640625%;
		
		div{
			margin-top: 41px;
			
			@media screen and (max-width: 767px){
				background-image: url('../img/interiorMobile.jpg');
				background-repeat: no-repeat;
				height: 120%;
				background-position: center;
				background-size: 100%;
			}
			@media screen and (min-width: 768px){
				background-image: url('../img/interior.jpg');
				background-repeat: no-repeat;
				height: 100%;
				background-position: center;
				background-size: 100%;
				width: 65%;
				margin-top: -25px;
				float: left;
			}
			@media screen and (min-width: 930px){
				margin-top: -10px;
				height: 99%;
			}
			@media screen and (min-width: 1000px){
				margin-top: -28px;
			}
			@media screen and (min-width: 1024px){
				margin-top: -15px;
			}
			@media screen and (min-width: 1150px){
				margin-top: -9px;
			}
			@media screen and (min-width: 1281px){
				margin-top: -1px;
				height: 90%;
			}
			@media screen and (min-width: 1337px){
				margin-top: 19px;
				height: 80%;
			}
			img{
				width: 100%;
			}
	
		}
		
		p{
			padding-left: 15px;
			margin-top: 5%;
			font-family: avenir next;
			font-size: 0.9em;
			line-height: 1.5em;
			padding-bottom: 10%;
			@media screen and (min-width: 768px){
				width: 30%;
				margin-top: 10%;
				float: right;
				padding-left: 0px;
				line-height: 1.4em;
			}
			@media screen and (min-width: 900px){
				line-height: 1.7em;
			}
			@media screen and (min-width: 1000px){
				line-height: 2em;
				font-size: 0.95em;
			}
			@media screen and (min-width: 1140px){
				font-size: 0.99em;
				line-height: 2.3em;
				margin-top: 8%;
			}
			@media screen and (min-width: 1336px){
				font-size: 1.2em;
				line-height: 2.2em;
			}
			
		}
	}
}

